'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace f
*/
var f = {};

/**
* @name cdf
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/cdf}
*/
import cdf from '@stdlib/stats-base-dists-f-cdf';
setReadOnly( f, 'cdf', cdf );

/**
* @name F
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/ctor}
*/
import F from '@stdlib/stats-base-dists-f-ctor';
setReadOnly( f, 'F', F );

/**
* @name entropy
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/entropy}
*/
import entropy from '@stdlib/stats-base-dists-f-entropy';
setReadOnly( f, 'entropy', entropy );

/**
* @name kurtosis
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/kurtosis}
*/
import kurtosis from '@stdlib/stats-base-dists-f-kurtosis';
setReadOnly( f, 'kurtosis', kurtosis );

/**
* @name mean
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/mean}
*/
import mean from '@stdlib/stats-base-dists-f-mean';
setReadOnly( f, 'mean', mean );

/**
* @name mode
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/mode}
*/
import mode from '@stdlib/stats-base-dists-f-mode';
setReadOnly( f, 'mode', mode );

/**
* @name pdf
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/pdf}
*/
import pdf from '@stdlib/stats-base-dists-f-pdf';
setReadOnly( f, 'pdf', pdf );

/**
* @name quantile
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/quantile}
*/
import quantile from '@stdlib/stats-base-dists-f-quantile';
setReadOnly( f, 'quantile', quantile );

/**
* @name skewness
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/skewness}
*/
import skewness from '@stdlib/stats-base-dists-f-skewness';
setReadOnly( f, 'skewness', skewness );

/**
* @name stdev
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/stdev}
*/
import stdev from '@stdlib/stats-base-dists-f-stdev';
setReadOnly( f, 'stdev', stdev );

/**
* @name variance
* @memberof f
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/dists/f/variance}
*/
import variance from '@stdlib/stats-base-dists-f-variance';
setReadOnly( f, 'variance', variance );


// EXPORTS //

export default f;
