'use strict';

/**
* Evaluate the quantile function for a studentized range distribution.
*
* @module @stdlib/stats-base-dists-studentized-range-quantile
*
* @example
* import quantile from '@stdlib/stats-base-dists-studentized-range-quantile';
*
* var y = quantile( 0.5, 3.0, 2.0 );
* // returns ~1.908
*
* y = quantile( 0.1, 3.0, 4.0 );
* // returns ~0.627
*
* var myquantile = quantile.factory( 3.0, 3.0 );
*
* y = myquantile( 0.5 );
* // returns ~1.791
*
* y = myquantile( 0.8 );
* // returns ~3.245
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
